'use strict';

window.onscroll = function (e) {
	var scrollY = window.pageYOffset;
	var fixedNav = document.querySelector("nav.site-nav");

	if (scrollY > 90) {
		if (!fixedNav.classList.contains('fixed')) {
			fixedNav.classList.add('fixed');
		}
	} else {
		if (fixedNav.classList.contains('fixed')) {
			fixedNav.classList.remove('fixed');
		}
	}
};